*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}
::-webkit-input-placeholder {
  color: rgba(255,255,255, .5);
}
button[disabled] {
  cursor: not-allowed;
}
body {
  position: relative;
  background: url(../img/bg.png) no-repeat 0 0 / cover;
  height: 100vh;
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
  color: #fff;
}
.wrapper {
  position: relative;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding: 1rem 7rem;

  .header, .block-form, .footer {
    transform: translateX(-20px);
    opacity: 0;
    visibility: hidden;
    transition: transform .3s ease, opacity .3s ease, visibility .3s ease;
  }

  &.show {
    .header, .block-form, .footer {
      transform: translateX(0px);
      opacity: 1;
      visibility: visible;
      transition: transform .3s ease, opacity .3s ease, visibility .3s ease;
    }
    .header {
      transition-delay: 100ms;
    }
    .block-form {
      transition-delay: 300ms;
    }
    .footer {
      transition-delay: 500ms;
    }
  }
}
.content {
  position: relative;
}

.header {
  .logo {
    font-size: 180px;
    font-family:  'Open Sans', sans-serif;
    margin: 0 0 0 -1rem;
    line-height: 1;
    letter-spacing: -.5rem;
    text-shadow: 7px 7px 10px rgba(16, 50, 99, .8);
  }
  .secondary {
    text-transform: uppercase;
    font-weight: 700;
    text-shadow: 8px 8px 20px rgba(16, 50, 99, .8);

    .text-bigger {
      display: block;
      font-size: 60px;
      margin: 0;
      line-height: 1;

    }
    .text-smaller {
      display: block;
      font-size: 36px;
      line-height: 1;
    }
  }
}
.block-form {
  position: relative;
  display: flex;
  flex-flow: column;
  padding: 15px 20px 20px;
  margin-top: 1.5rem;
  background: rgba(0, 46, 101, .8);
  //background: rgba(16, 50, 99, .8);
  border-radius: 2px;
  width: 640px;
  box-shadow: 2px 2px 7px #1c2751;
  z-index: 1;

  .block-number {
    position: relative;
    padding-bottom: 1.2rem;

    .text {
      color: #c1fcfe;
      font-size: 18px;
      padding-bottom: .5rem;
    }
    .inputs {
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      width: 100%;

      .input-telephone {
        position: relative;
        display: block;
        text-align: center;
        width: 190px;
        height: 28px;
        max-height: 100%;
        border-radius: 3px;
        margin-left: 10px;
        background: #00709d;
        font-weight: 500;
        border: 0;
        font-size: 18px;
        color: #fff;
        box-shadow: inset 2px 2px 2px rgba(8, 26, 58,.5);
      }
    }
    .error {
      position: absolute;
      top: 33px;
      right: 90px;
      display: none;
      align-items: center;

      &.show {
        display: flex;
      }

      span {
        display: block;
        margin-left: 8px;
        font-size: 14px;
        font-weight: 400;
      }
    }
  }
  .block-currency {
    padding-bottom: .7rem;

    .text {
      color: #c1fcfe;
      font-size: 18px;
      padding-bottom: .5rem;
    }
    .selectize-control {
      &.selectize-currency {
        .selectize-input {
          width: 290px;
          font-size: 16px;
        }
      }
    }
  }
  .block-notify {
    display: none;
    align-items: center;

    img {
      width: 30px;
      height: 30px;
    }

    span {
      display: block;
      margin-left: 8px;
      font-size: 14px;
    }

  }
  .show-accept.accept-phone, .show-error.same-phone {
    display: flex;
  }
  .btn-submit {
    display: block;
    width: 220px;
    height: 40px;
    padding: 7px 5px 3px;
    margin-top: .9rem;
    background: #71de81;
    border: none;
    border-radius: 3px;
    color: #fff;
    text-transform: uppercase;
    transition: all .3s ease;

    &.off {
      background: #20975f;
      transition: all .3s ease;
    }
  }
}
.footer {
  position: relative;
  margin-top: 10rem;
  z-index: 0;

  .insurance-block {
    display: flex;
    align-items: center;

    .insurance-text {
      display: flex;
      flex-flow: column;
      text-transform: uppercase;
      padding-left: 15px;

      .text-bigger {
        display: block;
        font-size: 32px;
      }
      .text-smaller {
        display: block;
        font-size: 22px;
      }
    }
  }
}
.selectize-control {
  &.single {
    .selectize-input:after {
      background: url(../img/dropdown-arrow.png) no-repeat 0 0;
      height: 10px;
      width: 15px;
      border: 0;
      margin: 0 !important;
      right: 7px;
      top: 50%;
      transform: translateY(-50%) rotate(0);
      transform-origin: center;
      transition: transform .3s ease;
    }
    .selectize-input.dropdown-active:after {
      transform: translateY(-50%) rotate(180deg);
      transition: transform .3s ease;
    }
    .selectize-dropdown {
      background: #015581;
      border: none;
      color: #add7fb;
    }
    .selectize-dropdown .active {
      background: rgba(0,0,0, .1);
      color: #fff;
    }
  }
  .selectize-input {
    display: flex !important;
    align-items: center;
    width: 90px;
    height: 28px;
    padding: 8px 10px 6px;
    border-radius: 3px;
    font-weight: 500;
    background: #00709d !important;
    border: 0;
    font-size: 18px;
    color: #fff;
    box-shadow: inset 2px 2px 2px rgba(8, 26, 58,.5) !important;

    input {
      display: none;
    }
  }
}
